* {
  margin: 0;
  box-sizing: border-box;
}

body {
  padding: 4px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #222;
  font-weight: 300;
  font-size: 11pt;
  background-color: #FFF;
}

div.app {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.container.center {
  justify-content: center;
}

.container-v-bottom {
  display: flex;
  align-items: flex-end;
}

.container-v-center {
  display: flex;
  align-items: center;
}

.right {
  text-align: end;
  justify-content: flex-end;
}

.container-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-vert-align {
  display: flex;
  align-items: flex-start;
}

.flex-between {
  display: flex;
  justify-content: space-between;
}

.padding {
  padding: 4px;
}

.full-width {
  width: 100%;
}

.one-third {
  width: 33.33%;
}

.two-third {
  width: 66.66%
}

.one-half {
  width: 50%;
}

.one-half.xs{
  width: 50%;
}

.one-fourth {
  width: 25%;
}

.three-fourth {
  width: 75%;
}

.one-third.overflow-scroll {
  max-width: 33.33%;
  overflow-x: scroll;
}

.card {
  box-shadow: 0 0 2px #CCC;
  border-radius: 4px;
  overflow: hidden;
  background-color: #EEE;
}

.card.medium {
  height: 353px;
}

.card.x-small {
  height: 113px;
}

.card.y-scrollable {
  overflow-y: scroll;
}

.card-header {
  padding: 16px 12px;
  font-weight: 400;
  font-size: 10pt;
  box-shadow: 0 1px 0 #FFF;
  display: flex;
  justify-content: space-between;
}

.card-body {
  padding: 8px;
}

.card-body.scroll-h {
  scrollbar-width: auto;
  overflow-x: scroll;
  overflow-y: hidden;
}

form {
  padding: 8px 0 0 0;
}

select {
  width: 100%;
  padding: 2px;
}

.align-right {
  text-align: end;
}

.align-center {
  text-align: center;
}

span.text-medium {
  font-size: 12pt;
}

span.text-big {
  font-size: 32px;
}

span.text-bigger {
  font-size: 30pt;
}

.bordered {
  box-shadow: 0 0 1px #DDD;
}

.bg-black {
  background-color: #333;
  color: #FFF;
}

table {
  width: 100%;
  background-color: inherit;
  overflow-x: scroll;
  scrollbar-width: thin;
  box-shadow: 0 0 1px #444;
}

tr:nth-child(even) {
  background-color: #DDD;
}

tr:not(:last-child) {
  /* border: 1px solid #333; */
  box-shadow: 0 1px 0 #DDD;
}

td:not(:last-child) {
  box-shadow: 1px 0 0 #DDD;
}

td, th {
  padding: 8px;
  text-align: start;
}

th {
  font-weight: 400;
}

thead {
  background-color: #333;
  color: #FFF;
}

td {
  font-size: 10pt;
}

.custom-tooltip {
  background-color: #EFEFEF;
  padding: 8px;
}

.modal {
  padding: 128px;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.920);
  position: fixed;
  top: 0;
  left: 0;
}

.white {
  background-color: white;
  box-shadow: 0 0 1px #666;
}

.padding-medium {
  padding: 8px;
}

span.close-modal-btn, span.open-modal-btn {
  box-shadow: 0 0 1px #444;
  padding: 4px 8px;
  color: #222;
  cursor: pointer;
}

span.close-modal-btn:hover {
  color: #FF6492;
}

span.open-modal-btn:hover {
  background-color: #2d5186;
  color: #FFF;
}

div.focused .card {
  background-color: #2d5186;
  color: #FFF;
}

div.bottom-bordered {
  border: 2px solid #2d5186;
}

div.tab-selector {
  display: flex;
  box-shadow: 0 0 1px #AAA;
  border-radius: 4px;
  overflow: hidden;
}

div.tab {
  padding: 8px 16px;
  cursor: pointer;
}

div.tab.active {
  background-color: #2d5186;
  color: #FFF;
}

span::selection, text::selection {
  background-color: #2d5186;
  color: #FFF;
}

b::selection, td::selection, tspan::selection {
  background-color: #2d5186;
  color: #FFF;
}

@media only screen and (max-width: 1280px) {
  .one-third{
    width: 50%;
  }
  .two-third, .one-half, .one-fourth, .three-fourth {
    width: 100%;
  }
}

@media only screen and (max-width: 640px) {
  .one-third {
    width: 100%;
  }
}

@media only screen and (max-width: 480px) {
  .one-half.xs {
    width: 100%;
  }
}