.loader {
    border: 8px solid #ccc;
    /* Light grey */
    border-top: 8px solid #f3f3f3;
    /* Blue */
    border-radius: 50%;
    width: 150px;
    height: 150px;
    animation: spin 2s linear infinite;
}
.loader-container {
    /* border: 4px solid #f3f3f3; */
    border-radius: 50%;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}